import React from 'react';
import './Header.css'
import logo from '../../assets/images/logo.png'
import * as axios from 'axios';
import cheerio from "cheerio";

class Header extends React.Component {
    componentDidMount() {
        axios.get(`https://cors-anywhere.herokuapp.com/https://magnit.ru/promo/?geo=moskva`).then(({data})=> {
            const $ = cheerio.load(data);
            const cost = $('.label__price .label__price-integer').text();
        })
    }

    render() {
        return (
            <div className='header'>
                <div className="logo-block">
                    <img src={logo} alt="mdtp"/>
                </div>
                <div className='heading-block'>
                    <h1 className='heading'>Магазины и сети со скидками в Омске</h1>
                </div>
            </div>
        );
    }
}

export default Header;