import React from 'react';
import './Shop-Item.css'

const ShopItem = (props) => {
    return (
        <div className="shop__item">
            <div className='shop__photo'>
                <a href={props.urlShop} target="_blank" rel="noreferrer" ><img src={props.url} alt=""/></a>
            </div>
            <div className="retail-root">
                <div className='retail-info'>
                    <div className="retail__offers"><span className='offers'>{props.offers}</span> <span>предложения</span></div>
                    <div className="retail__shops"><span className='shops'>{props.shops}</span> <span>магазинов</span></div>
                </div>
                <div className="retail__discount">
                    до<span className='retail'> {props.discount}%</span>
                </div>
            </div>
        </div>
    );
};

export default ShopItem;