const MainPageReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'CHANGE-SEARCH-QUERY':
            return {
                ...state,
                searchQuery: action.payload
            }
        case 'SORT-PRODUCTS':
            return {
                ...state,
                sortedProducts:action.payload
            }
        case 'SET-PRODUCT':
            return {
                ...state,
                product:action.payload
            }
        case 'SET-VISIBLE':
            return {
                ...state,
                visible:!state.visible
            }
        default:
            return state
    }
};

export const changeSearchQuery = (query) => ({type: 'CHANGE-SEARCH-QUERY', payload: query})
export const sortProducts = (products) => ({type:'SORT-PRODUCTS',payload:products})
export const setProduct = (product) => ({type:'SET-PRODUCT',payload:product})
export const setVisible = () => ({type:'SET-VISIBLE'})


export default MainPageReducer;