import React from 'react';
import {Button as BaseButton} from 'antd';
import {SearchOutlined} from '@ant-design/icons';
import {connect} from "react-redux";

import './SearchProduct.css'
import {changeSearchQuery, setProduct, setVisible, sortProducts} from "../../Redux/Reducers/MainPageReducer";


const SearchProduct = (props) => {
    let onChangeInput = (e) => {
        props.changeSearchQuery(e.target.value);
    }
    let searchProduct = () => {
        let products = props.itemsProduct;
        let query = props.searchQuery;
        props.sortProducts(products.filter(e => {
            return e.product.toLowerCase().indexOf(query.toLowerCase()) >= 0;
        }));
        if (props.sortedProducts){
            props.setProduct(props.sortedProducts.sort((a, b) => {
                return a.price > b.price ? 1 : -1;
            }))
        }
    }
    return (
        <div className='search-product-block'>
            <div className='search-product'>
                <input className='search' type="text" onInput={(e) => onChangeInput(e)} value={props.searchQuery}
                       placeholder='Поиск товаров'/>
                <BaseButton onClick={searchProduct} type="primary" icon={<SearchOutlined/>}>
                    Найти
                </BaseButton>
            </div>
            <div className="area-product">
                <div className="shop">
                    <img
                        src={props.product && props.product[0].url}
                        alt=""/>
                </div>
                <div className='product-price'>
                    <div className="product">{props.product && props.product[0].product}&nbsp;</div>
                    <div className="price">{props.product && props.product[0].price}<span>&nbsp;руб</span></div>
                </div>
            </div>
        </div>
    );
};
let mapStateToProps = (state) => ({
    searchQuery: state.mainPage.searchQuery,
    itemsProduct: state.mainPage.itemsProduct,
    sortedProducts: state.mainPage.sortedProducts,
    product: state.mainPage.product,
    visible: state.mainPage.visible
})
export default connect(mapStateToProps, {changeSearchQuery, sortProducts, setProduct, setVisible})(SearchProduct);